import { addNotification } from '../Notification';

const prepeareCallback = (callback, data) => {
    if (data.success === false) {
        if (callback.error !== null) callback.error(data);
        if (data.code) {
            let varia = {};

            if (data.input) varia = {valueEmpty: `'${data.input}'`};
            if (data.wait)  varia = {valueZero: data.wait};

            addNotification.error(
                data.code, 
                varia
            );
        }

        return;
    }

    if (callback.success !== null) callback.success(data);
};

export default prepeareCallback;