import bodyWithSign from './bodyWithSign';
import prepeareCallback from './prepeareCallback';
import { API_URL } from '../SETTINGS';

const API = async (option) => {
    if (option.query) {
        option.query.language = localStorage.getItem("W-Language");
    }

    fetch(API_URL + "/" + option.method, {
        "method": "POST",
        "headers": Object.assign(
            {
                "content-type": "application/json",
                "accept": "application/json"
            },
            option.Auth ? {
                "x-W-Id": localStorage.getItem("W-Id"),//TODO извлекать из localstorage
                "x-W-Token": localStorage.getItem("W-Token"),//TODO извлекать из localstorage
            } : {}
        ),
        "body": bodyWithSign(option.query)
    })
    .then(response => response.json())
    .then(response => {
        prepeareCallback(option.callback, response);
    })
    .catch(err => {
        prepeareCallback(option.callback, {
            success: false,
            code: "error.CantRequest"
        });
    });
}

API.sendAuth = (path, query, callbackSuccess = null, callbackError = null) => {
    API({
        method: path,
        query: query,
        Auth: true,
        callback: {
            error: callbackError,
            success: callbackSuccess
        }
    });
}

API.send = (path, query, callbackSuccess = null, callbackError = null) => {
    API({
        method: path,
        query: query,
        Auth: false,
        callback: {
            error: callbackError,
            success: callbackSuccess
        }
    });
}

export default API;