import { sha256 } from 'js-sha256';
import { SIGN_SECRET } from '../SETTINGS';

const bodyWithSign = (Query) => {
    Query['client'] = Date.now();

    const hash = sha256.hmac.create(SIGN_SECRET);

    hash.update(
        JSON.stringify(
            Object.keys(Query).sort().reduce((accum, key) => {
                accum[key] = Query[key];
    
                return accum;
            }, {})
        )
    );

    return JSON.stringify({
        ...Query, 
        sign: hash.hex()
    })
};

export default bodyWithSign;