import {useState} from 'react';
import { useTranslation } from "react-i18next";

import CustomInput from './CustomInput';
import Form from './Common/Form';
import Button from './Common/Button';
import Buttons from './Common/Buttons';
import {addNotification} from '../../Notification';
import API from '../../API';

import svgAssetUnauth from '../../svg/AssetUnauth';

const Reset = ({changeType}) => {
    const { t } = useTranslation(null, {keyPrefix:'modal'});

    const [emailValue, setEmailValue] = useState(false); 
    
    const handlerSuccess = () => {
        changeType('ResetSuccess');
    }

    const handlerReset = () => {
        if (!emailValue) {
            return addNotification.error("error.FillAllData");
        }

        API.send("resert/request", 
            {
                email: emailValue
            }, 
            handlerSuccess
        );
    };
    return (
        <>
            <Form>
                <CustomInput 
                    inputType="email" 
                    langCodePlaceholder="Email" 
                    returnValue={(data) => setEmailValue(data)} 
                />

                <Buttons>
                    <Button onClick={() => changeType('Auth')}>
                        {svgAssetUnauth.user}
                        {t('Sign In')} 
                    </Button>

                    <Button green="true" onClick={() => handlerReset()}>
                        {svgAssetUnauth.key} 
                        {t('Reset password')}
                    </Button>
                </Buttons>
            </Form>
        </>
    );
}

export default Reset;