import {useState} from 'react';
import { useTranslation } from "react-i18next";

import CustomInput from '../CustomInput';
import API from '../../../API';
import {addNotification} from '../../../Notification';

import styles from './PhoneInput.module.scss';
import svgAssetUnauth from '../../../svg/AssetUnauth';


const PhoneInput = () => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });
    const [iddValue, setIddValue] = useState(false); 
    const [phoneNumberValue, setPhoneNumberValue] = useState(false); 
    const [waitButton, setWaitButton] = useState(false); 

    const handlerError = (data) => {
        if (data.wait) checkWait(data.wait);
    }

    const handlerSuccess = (data) => {
        addNotification.success("success.CallSend");

        if (data.wait) checkWait(data.wait);
    }

    const handlerSendCode = () => {
        if (!iddValue || !phoneNumberValue) {
            return addNotification.error("error.FillAllData");
        }

        API.send("phone/code", 
            {
                phone: iddValue + "" + phoneNumberValue
            }, 
            handlerSuccess, 
            handlerError
        );
    };

    const checkWait = async (count) => {
        if (count <= 0) return setWaitButton(false);

        setWaitButton(count);

        setTimeout(() => {
            --count;
            
            checkWait(count);
        }, 1000);
    }

    return (
        <>
            <div class={styles.phoneInput}>
                <CustomInput 
                    customClass={styles.first} 
                    langCodePlaceholder="+263" 
                    inputType="IDD"  
                    returnValue={(data) => setIddValue(data)} 
                />
                <CustomInput 
                    customClass={styles.second} 
                    langCodePlaceholder="Phone Number" 
                    inputType="phoneNumber" 
                    returnValue={(data) => setPhoneNumberValue(data)} 
                />
            </div>

            <CustomInput
                langCodePlaceholder="Last 4 digit number"
                inputType="code"
                configButton={
                    waitButton ? 
                    {
                        svg: svgAssetUnauth.clock,
                        text: `${waitButton} s`,
                        disabled: true
                    }
                    :
                    {
                        svg: svgAssetUnauth.call,
                        text: t('Call'),
                        callback: () => handlerSendCode()
                    }
                }
            />
        </>
    );
}

export default PhoneInput;