import { useTranslation } from "react-i18next";

import Form from './Common/Form';
import Button from './Common/Button';

import svgAssetUnauth from '../../svg/AssetUnauth';
import svgGlobal from '../../svg/Global';

import styles from './ResetSuccess.module.scss';

const ResetSuccess = ({changeType}) => {
    const { t } = useTranslation(null, {keyPrefix:'modal'});

    return (
        <>
            <Form hideDetails={true}>

                <div className={styles.box}>
                    {svgGlobal.Tick}
                    <div className={styles.text}>{t('Reset instructions have been sent to your email')}</div>
                </div>

                <Button classNameCustom={styles.big} onClick={() => changeType('Auth')}>
                    {svgAssetUnauth.user}
                    {t('Back to login')} 
                </Button>
            </Form>
        </>
    );
}

export default ResetSuccess;