import styles from './Button.module.scss';

const Button = (props) => {
    return (        
        <div 
            className={
                `${
                    styles.button
                } ${
                    props.green ? styles.green : ''
                } ${
                    props.classNameCustom || ''
                }`
            }

            {...props}
        >
            {props.children}
        </div>
    );
}

export default Button;