import styles from './InputState.module.scss';

import svgGlobal from '../../../svg/Global';

const InputState = ({stateType}) => {
    return (
        <div className={`${styles.state} ${
                (stateType === "bad" && styles.bad) ||
                (stateType === "good" && styles.good) || ""
            }`}>
            {
                (stateType === "bad" && svgGlobal.Close) ||
                (stateType === "good" && svgGlobal.Tick) || ""
            }
        </div>
    );
};

export default InputState;
                
